import React, {Component} from 'react';
import axiosAPI from "./axiosAPI";
import addImage from "./refresh--v1.png";
import noImage from './no-image-available.png';
import './App.css';

class App extends Component {

    state = {
        fromId: '',
        toId: '',
        boxes: [],
        inputs: [{id:0,value: '',removed: false,image: null, price: null}],
        forInformation: [],
        allPrices: [],
        totalPrice: ""
    };

    async componentDidMount(){
        const res = await axiosAPI.get('/item/');
        this.setState({boxes: res.data});
    };

    inputValHandler = (e) => {
        this.setState({[e.target.name]: e.target.value});
    };

    inputsValueForBoxes = (id,e,inputs) => {
        if(!inputs){
            const input = {
                id: id,
                value: e.target.value,
                removed: false,
            };
            const findPrice = this.state.boxes.find(box => box.nameRU === input.value);
            if(findPrice){
                input.price = findPrice.price;
                if(findPrice.image){
                    input.image = findPrice.image;
                }
            }
            this.state.inputs.splice(id,1,input);
            const filteredRemove = this.state.inputs.filter(remove => remove.removed === false);
            this.setState({[this.state.inputs[id]]: input,forInformation: filteredRemove});
        }else {
            const filteredRemove = this.state.inputs.filter(remove => remove.removed === false);
            this.setState({forInformation: filteredRemove});
        }

        if(this.state.inputs[id].value === "") {
            const removeInfo = this.state.forInformation.filter(input => input.id !== id);
            this.setState({forInformation: removeInfo});
        }
        this.totalPriceHandler();
    };

    addInputHandler = (addId) => {
        const input = {
            id: this.state.inputs.length,
            value: this.state.inputs[addId].value,
            removed: false,
            image: !this.state.inputs[addId].image ? null : this.state.inputs[addId].image,
            price: this.state.inputs[addId].price === null ? null : this.state.inputs[addId].price
        };

        this.setState({
            inputs: [...this.state.inputs,input],
            forInformation: [...this.state.forInformation,input]
        });

        this.totalPriceHandler();
    };

    addJustInput = () => {
        this.setState({inputs: [...this.state.inputs,{id: this.state.inputs.length,value: "",removed: false}]});
    };

    deleteInputHandler = (e,id) => {
        e.nativeEvent.path[1].classList.add('active');
        const input = {
            id: id,
            value: "",
            removed: true,
            image: null,
            price: null
        };
        this.state.inputs.splice(id,1,input);
        this.setState({[this.state.inputs[id]]: input});
        this.inputsValueForBoxes(id,e,this.state.inputs);
        setTimeout(() => {
            e.nativeEvent.path[1].remove();
        },1000);
    };

    totalPriceHandler = () => {
        const mass = [0];
        for (let i = 0; i < this.state.forInformation.length; i++) {
            if(!isNaN(this.state.forInformation[i].price)){
                mass.push(this.state.forInformation[i].price);
            }
            mass.reduce((a,b) => {
                if(mass[0] === 0){
                    mass.splice(0,1);
                }
                return a + b;
            });
        }
        this.setState({totalPrice: mass.reduce((a,b) => a + b * 460),allPrices: mass.toString().split(',').join(' + ')})
    };

    componentDidUpdate(prevProps, prevState, snapshot) {
        if(prevState.forInformation !== this.state.forInformation){
            this.totalPriceHandler();
        }
    }

    sendInfo = async () => {
        const object = {
            fromId: this.state.fromId,
            toId: this.state.toId,
            items: [

            ]
        };
        for (let i = 0; i < this.state.forInformation.length; i++) {
            object.items.push(this.state.forInformation[i].value);
        }
        await axiosAPI.post('/buy/',object);
        alert("Ваш заказ принят!");
    };

    clearALl = () => {
        window.location.reload();
    };

    render() {
        return (
            <div className="App">
                <div className="form">
                    <div className="inputs_for_id">
                        <input type="text" name="fromId" onChange={this.inputValHandler} placeholder="from id"/>
                        <input type="text" name="toId" onChange={this.inputValHandler} placeholder="to id"/>
                    </div>
                    <div className="inputs_for_box">
                        {this.state.inputs && Object.keys(this.state.inputs).map(input => (
                            <div key={input} id={input}>
                                <input type="text" list="boxes" defaultValue={this.state.inputs[input].value} name="box" placeholder="Кронный ящик x?"
                                       onChange={(e) => this.inputsValueForBoxes(this.state.inputs[input].id,e)}/>
                                <button onClick={(e) => this.addInputHandler(this.state.inputs[input].id,e)}>
                                    <img src={addImage} alt=""/>
                                </button>
                                {input > 0 ? (
                                    <button className="delete" onClick={(e) => this.deleteInputHandler(e,this.state.inputs[input].id)}>
                                        X
                                    </button>
                                ) : (
                                    <></>
                                )}
                                <datalist id="boxes">
                                    {this.state.boxes && Object.keys(this.state.boxes).map(info => (
                                        <option key={info}>{this.state.boxes[info].nameRU}</option>
                                    ))}
                                </datalist>
                            </div>
                        ))}
                    </div>
                    <div className="total_price">
                        <p>
                            Total:{'(' + this.state.allPrices + ') * 460 = ' + this.state.totalPrice}
                        </p>
                    </div>
                    <button className="addJustInput" onClick={this.addJustInput}>
                        добавить
                    </button>
                    <div className="btns">
                        <button className="sendInfo" onClick={this.sendInfo}>
                            отправить
                        </button>
                        <button className="clear" onClick={this.clearALl}>
                            очистить все
                        </button>
                    </div>
                    <div className="product_info">
                        {this.state.forInformation && Object.keys(this.state.forInformation).map(info => (
                            <div key={info}>
                                <div className="kronn_block">
                                    {this.state.forInformation[info].image ? (
                                        <img width="300" src={this.state.forInformation[info].image} alt=""/>
                                    ) : (
                                        <img width="300" src={noImage} alt=""/>
                                    )}
                                    <div className="title_kronn">
                                        <p className="name">{this.state.forInformation[info].value}</p>
                                        <p className="price">{this.state.forInformation[info].price}</p>
                                    </div>
                                </div>
                            </div>
                        ))}
                    </div>
                </div>
            </div>
        );
    }
}

export default App;